@import "./../../../../variables";


.filmsWrapper {
    color: #fff;

    .filmsCategory {
        margin: 3rem;
        background-color: #141517;
        padding: 2rem;
        border-radius: 25px;
        border: 1px solid #212224;

        h4 {
            color: $gray;
            text-transform: uppercase;
            margin-top: 0;
            letter-spacing: 2px;
        }


        .catBlock{
            flex-direction: column;
            justify-content: center;


            .filmItem {
                position: relative;
                border-radius: 10px;
                background-size: cover;
                background-repeat: no-repeat;
                width: 100%;
                height: 16rem;
                display: flex;
                justify-content: center;
                align-items: start;
                flex-flow: wrap column;
                background-position: center;
                background-repeat: no-repeat;

                @media only screen and (max-width: 600px) { 
                    height: 10rem;
                }

                &:hover {
                    transition: all .2s ease-in-out;
                    transform: scale(0.96);
                }
                
            
                &::before {
                    content: '';
                    background-image: linear-gradient(to right, #0f1011ab,  transparent);
                    width: 170%;
                    height: 100%;
                    position: absolute;
                    top: 0;
                    left: 0;
                    z-index: 1;
                }
                * {
                    z-index: 2;
                }

                    a {
                        margin: 3rem 0 0 1rem;
                        color: #fff;
                        font-weight: 600;
                        font-size: 29px;
                        transition: color 0.4s ease-in-out;

                        @media only screen and (max-width: 600px) { 
                            font-size: 24px;
                        }

                        @media only screen and (max-width: 460px) { 
                            font-size: 19px;
                            margin-top: 0;
                        }
                        
                        &:hover {
                            color: $gray;
                        }

                    }

                    span {
                        margin-left: 1rem;
                        color: #cdcdcd;
                        font-weight: 600;
                        font-size: 14px;

                        @media only screen and (max-width: 600px) { 
                            font-size: 12px;
                        }
                    } 
            }
        }
    }
}
