@import '../../../variables';

.header {
    position: relative;
    padding: 20px 40px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    > div:first-child {
        height: 50px;
        width: 80px;

        display: flex;
        align-items: center;
        > a{
            width: 100%;
            display: flex;
            align-items: center;
            
            svg{
                height: 100%;
            }
        }
    }

    .notification {
        position: relative;
        margin-right: 1.5rem;
        cursor: pointer;

        >i{
            color: #fff;
            font-size: 26px;
        }

        > span {
            background-color: #c62e21;
            width: 10px;
            height: 10px;
            top: 0;
            right: 0;
            position: absolute;
            background-repeat: 50%;
            border: 1px solid #0f1011;
            border-radius: 50%;
        }
    }

    .profile-wrapper {
        display: flex;
        align-items: center;
    }

    .profile {
        position: relative;
        display: flex;
        align-items: center;

        .dropdown{
            position: absolute;
            top: 108%;
            right: 0;
            border: 1px solid red;
            width: 10rem;
            height: 7rem;
            z-index: 2;
            display: flex;
            justify-content: center;
            align-items: start;
            flex-flow: column;
            background-color: #0f1011;
            border: 1px solid rgb(28, 28, 35);
            border-radius:10px;
            -webkit-box-shadow: 3px 1px 58px 14px rgba(0, 0, 0, 0.32);
            -moz-box-shadow: 3px 1px 58px 14px rgba(0, 0, 0, 0.32);
            box-shadow: 3px 1px 58px 14px rgba(0, 0, 0, 0.32);

            >a {
                text-decoration: none;
                display: block;
            }
            
            button, a {
                font-size: 15px;
                margin: 0 0 1rem 1rem;
                padding: 5px 30px;
                border: none;
                background-color: transparent;
                color: $gray;
                border-radius: 15px;

                &:hover {
                    background-color: #141516;
                }

            }
        }

        .avatar {
            height: 35px;
            width: 35px;
            display: flex;
            align-items: center;
            justify-content: center;
            margin-right: 0.3rem;
            overflow: hidden;
            border-radius: 50%;
            cursor: pointer; 
        }

        >i {
            color: #fff;
            font-size: 15px;
            cursor: pointer;
            transition: color 0,1s ease-in-out; 

            &:hover {
                color: $red;
            }
        }
    }    
}

.query {
    display: none;
    position: absolute;
    width: 12.5rem;
    color: white;
    font-size: 16px;
    border: 1px solid rgba(155, 154, 151, 0.4);
    border-radius: 50%;
    top: 44px;
    left: 248px;
    z-index: 2;
    background: #000;
    padding: 0.5rem 1rem;
    display: flex;
    align-items: center;
    border-radius: 0 0 30px 30px;

    @media only screen and (max-width: 768px) { 
		left: 136px;
	}

    @media only screen and (max-width: 540px) { 
        width: 100%;
        left: 110px;
	}


    > ul {
        text-decoration: none;
        list-style: none;
        color: $gray;
        li {
            padding: 5px 0;

            a {
                font-style: 15px;
                color: $gray;


                :last-child {
                    font-size: 10px;
                }

                span {
                    display: flex;
                    flex-direction: column;
                }

                &:hover {
                    color: #ae1d1d;
                }

            }
        }
    }
}

